import { useState, useEffect } from "react";
import { Link } from "react-router-dom";

export default function Signup() {
    const [error, setError] = useState(null)
    const [firstname, setFirstname] = useState("")
    const [name, setName] = useState("")
    const [birthday, setBirthday] = useState("")
    const [phone, setPhone] = useState("")
    const [address, setAdress] = useState("")
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [showPassword, setshowPassword] = useState(false)

    async function handleSumbmit(e) {
        try {
            e.preventDefault()

            const newUser = { firstname, name, birthday, phone, address, email, password }
            console.log(newUser)

            const _error = {}

            if (!firstname) {
                _error.firstname = "  "
            }

            if (!name) {
                _error.name = "  "
            }

            if (!birthday.match(/^\d{4}-(0[1-9]|1[012])-(0[1-9]|[12][0-9]|3[01])$/)) {
                _error.birthday = " YYYY-MM-DD "
            }

            if (!phone) {
                _error.phone = "  "
            } else if (phone.match(/^(01[016789]{1})-[0-9]{3,4}-[0-9]{4}$/)) {
                _error.phone = "  "
            }

            if (!address) {
                _error.address = "  "
            }

            if (!email) {
                _error.email = "  "
            }
            else if (email.indexOf("@") === -1) {
                _error.email = "  "
            }

            if (!password) {
                _error.password = "  "
            } else if (!password.match(/[A-Za-z0-9]{8,20}$/)) {
                _error.password = "  8 , ,    "
            }

            if (Object.keys(_error).length > 0) {
                throw _error
            }

        } catch (error) {
            setError(error)
        }
    }

    useEffect(() => {
        document.title = ""
    }, [])

    return (
        <>
            <form onSubmit={handleSumbmit} className="w-3/4 flex flex-col justify-center m-auto mt-10">
                <div className="mb-2">
                    <label className="block">
                        
                        <input
                            type="text"
                            name="firstname"
                            onChange={({ target }) => setFirstname(target.value)}
                            className="border px-2 py-2 w-full rounded"
                        />
                    </label>
                    {error && <p className="text-red-500">{error.firstname}</p>}
                </div>

                <div className="mb-2">
                    <label className="block">
                        
                        <input
                            type="text"
                            name="name"
                            onChange={({ target }) => setName(target.value)}
                            className="border px-2 py-2 w-full rounded"
                        />
                    </label>
                    {error && <p className="text-red-500">{error.name}</p>}
                </div>
                <div className="mb-2">
                    <label className="block">
                        
                        <input
                            type="text"
                            name="birthday"
                            onChange={({ target }) => setBirthday(target.value)}
                            className="border px-2 py-2 w-full rounded"
                        />
                        <p className="text-xs text-[#767676] my-1">YYYY-MM-DD</p>
                    </label>
                    {error && <p className="text-red-500">{error.birthday}</p>}
                </div>
                <div className="mb-2 mt-4">
                    <label className="block">
                        
                        <input
                            type="text"
                            name="phone"
                            onChange={({ target }) => setPhone(target.value)}
                            className="border px-2 py-2 w-full rounded"
                        />
                        {error && <p className="text-red-500">{error.phone}</p>}
                    </label>
                </div>
                <div className="mb-2">
                    <label className="block">
                        ()
                        <select className="border px-2 py-2 w-full rounded">
                            <option value=""></option>
                            <option value="" className=""></option>
                            <option value="" className=""></option>
                            <option value="" className=""></option>
                        </select>
                    </label>
                </div>
                <div className="my-4">
                    <button
                        className="bg-[#2360a5] px-6 py-4 rounded-full text-white">
                         
                    </button>
                </div>
                <div className="mb-2">
                    <label className="block">
                         
                        <input
                            type="text"
                            className="border px-2 py-2 w-full rounded" disabled
                        />
                    </label>
                </div>
                <div className="mb-2">
                    <label className="block">
                         
                        <input
                            type="text"
                            name="address"
                            onChange={({ target }) => setAdress(target.value)}
                            className="border px-2 py-2 w-full rounded"
                        />
                    </label>
                    {error && <p className="text-red-500">{error.address}</p>}
                </div>
                <div className="mb-2">
                    <label className="block">
                        
                        <input
                            type="text"
                            className="border px-2 py-2 w-full rounded" disabled
                        />
                    </label>
                </div>
                <div className="mb-2">
                    <label className="block">
                         
                        <select className="border px-2 py-2 w-full rounded">
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                        </select>
                    </label>
                </div>
                <div className="mb-2">
                    <label className="block">
                        
                        <input
                            type="text"
                            name="email"
                            onChange={({ target }) => setEmail(target.value)}
                            className="border px-2 py-2 w-full rounded"
                        />
                        {error && <p className="text-red-500">{error.email}</p>}
                    </label>
                </div>
                <div className="mb-2">
                    <label className="block relative">
                        
                        <input
                            type={showPassword ? "text" : "password"}
                            name="password"
                            onChange={({ target }) => setPassword(target.value)}
                            className="font-sans border px-2 py-2 w-full rounded"
                        />
                        <button
                            type="button"
                            className="absolute right-4 top-8"
                            onClick={() => setshowPassword(!showPassword)}
                        >
                            {showPassword ? '' : ''}
                        </button>
                    </label>
                    {error && <p className="text-red-500">{error.password}</p>}
                </div>
                <div className="my-4 flex items-center">
                    <input
                        type="checkbox"
                        id="check1"
                        className="w-14 h-14 bg-white border border-black rounded mr-4
                            checked:bg-[#2360a5] checked:border-transparent focus:outline-none checked:text-white-600"
                    />
                    <label className="block" for="check1"> ,       IKEA  
                         ,      !</label>

                </div>
                <div className="my-2 ml-8 flex">
                    <input
                        type="checkbox"
                        id="check2"
                        className="w-6 h-6 bg-white border border-black rounded mr-4
                            checked:bg-[#2360a5] checked:border-transparent focus:outline-none checked:text-white-600"
                    />
                    <label className="block" for="check2"> </label>
                </div>
                <div className="my-2 ml-8 flex">
                    <input
                        type="checkbox"
                        id="check3"
                        className="w-6 h-6 bg-white border border-black rounded mr-4
                            checked:bg-[#2360a5] checked:border-transparent focus:outline-none checked:text-white-600"
                    />
                    <label className="block" for="check3"> </label>
                </div>
                <div className="my-2 ml-8 flex">
                    <input
                        type="checkbox"
                        id="check4"
                        className="w-6 h-6 bg-white border border-black rounded mr-4 align-middle
                            checked:bg-[#2360a5] checked:border-transparent focus:outline-none checked:text-white-600"
                    />
                    <label className="block" for="check4"> </label>
                </div>
                <div className="my-4 flex items-center">
                    <input
                        type="checkbox"
                        id="check5"
                        className="w-7 h-7 bg-white border border-black rounded mr-4
                            checked:bg-[#2360a5] checked:border-transparent focus:outline-none checked:text-white-600"
                    />
                    <label className="block" for="check5">()    </label>
                </div>
                <div className="my-4 flex items-center">
                    <input
                        type="checkbox"
                        id="check5"
                        className="w-7 h-7 bg-white border border-black rounded mr-4
                            checked:bg-[#2360a5] checked:border-transparent focus:outline-none checked:text-white-600"
                    />
                    <label className="block" for="check5">()   </label>
                </div>
                <button className="w-full bg-black rounded-full mt-4 p-4">
                    <p className="text-white"></p>
                </button>
                <div className="mt-8">
                    <p className="text-lg">   ?</p>
                    <Link to="/account/Login">
                    <p className="mt-4 text-center text-lg underline underline-offset-4"></p>
                    </Link>
                </div>
                <div className="mb-20"></div>
            </form>
        </>
    )
}

